// @flow
import React, { useEffect, Fragment } from 'react';
import type { Element } from 'react';
import Paper from '@material-ui/core/Paper';
import { ViewState } from '@devexpress/dx-react-scheduler';
import {
  Scheduler,
  WeekView,
  Appointments,
  AppointmentTooltip,
} from '@devexpress/dx-react-scheduler-material-ui';
import _get from 'lodash/get';
import appointments from './mock/today-appointments';
import DayScaleCell from './DayScaleCell';
import TimeTableCell from './TimeTableCell';
import Appointment from './Appointment';
import TooltipContent from './TooltipContent';
import {
  DEFAULT_DURATION,
  CALENDAR_CONFIG_FIELDS,
  ERROR_MESSAGES,
} from './helpers';
import { ProfilesService } from '../../services/YouCanBook';
import { connect } from 'react-redux';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { setConfig } from './redux/Actions';
import { createStructuredSelector } from 'reselect';
import _isEmpty from 'lodash/isEmpty';
import { toastr } from 'react-redux-toastr';

const Calendar = ({
  config,
  setConfig,
  profileId,
}): Element<'div' | typeof Fragment> => {
  useEffect(() => {
    (async () => {
      try {
        const data = await ProfilesService.getProfileById({
          id: profileId,
          fields: CALENDAR_CONFIG_FIELDS,
        });
        setConfig(_get(data, 'times') || {});
      } catch (error) {
        const { message } = error || {};

        toastr.error(ERROR_MESSAGES.getTimeConfig, message);
      }
    })();

    return () => {
      ProfilesService.cancel();
    };
    // eslint-disable-next-line
  }, []);

  if (_isEmpty(config)) return <></>;

  return (
    <Paper>
      <Scheduler data={appointments}>
        <ViewState />
        <WeekView
          timeTableCellComponent={TimeTableCell}
          dayScaleCellComponent={DayScaleCell}
          cellDuration={_get(config, 'slotLengthMinutes') || DEFAULT_DURATION}
        />
        <Appointments appointmentComponent={Appointment} />
        <AppointmentTooltip showCloseButton contentComponent={TooltipContent} />
      </Scheduler>
    </Paper>
  );
};

type PropsWithoutRedux = {};

const structuredSelector = createStructuredSelector({
  config: state => _get(state, ['calendar', 'config']),
});

const mapDispatchToProps = {
  setConfig,
};

export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(withErrorBoundary(Calendar)): Element<PropsWithoutRedux>);
