import axios from 'axios';
import _get from 'lodash/get';
import { PLACEHOLDER_AUTHORIZATION } from './constants';

export default class Request {
  constructor() {
    this.baseURL = `${process.env.REACT_APP_YOU_BOOK_ME_ENDPOINT}/${process.env.REACT_APP_YOU_BOOK_ME_ACCOUNT_ID}/`;
    this.cancelToken = null;
  }

  cancel() {
    if (this.cancelToken !== null) this.cancelToken();
  }

  axios({ method, baseURL = this.baseURL, url, data, params, headers = {} }) {
    const self = this;
    const { CancelToken } = axios;

    return new Promise((resolve, reject) => {
      const options = {
        method,
        url,
        baseURL,
        timeout: 30000,
        params,
        data,
        headers: { ...headers, Authorization: PLACEHOLDER_AUTHORIZATION },
        cancelToken: new CancelToken(function executor(c) {
          self.cancelToken = c;
        }),
      };

      axios.request(options).then(
        res => {
          resolve(
            typeof res.data === 'object' ? res.data : JSON.parse(res.data),
          );
        },
        error => {
          const status = _get(error, ['response', 'status']);
          const message = _get(error, ['response', 'data', 'message']);
          const errors = _get(error, ['response', 'data', 'errors']);
          const errorRes = { status, message, errors };
          reject(errorRes);
        },
      );
    });
  }
}
